import React from 'react';
import { Link } from "gatsby";
import Image from "../../../components/Image";
import Author from "../../Author";

import "./styles.scss";

const HeadlineCard = ({ data }) => {
    const {frontmatter} = data;
    const options ={
        sizes: "(max-width: 480px) 150px, 400px",
    };
    const src = frontmatter.cover_photo;
    return (
        <Link to={`/${frontmatter.slug}`} className={`BigHeadlineCard`}>
            <div className="imgContainer">
                <Image src={src} alt={frontmatter.title} options={options} />
            </div>

            <div className="descriptionContainer">
                <div className="title">
                    {frontmatter.title}
                </div>
                <div className="date">
                    {frontmatter.date}
                </div>
                <div className="AuthorBubbleContainer">
                    <Author id={frontmatter.author} />
                </div>
            </div>
        </Link>
    );
};

export default HeadlineCard;
