import React from "react";
import { Link } from "gatsby";

import Image from "../Image";
import Author from "../Author";

import "./styles.scss";

const Card = ({ data }) => {
    const {frontmatter} = data;
    const options = {
        sizes: "(max-width: 480px) 150px, 320px",
    };
    const src = frontmatter.cover_photo;
    return (
        <article className="ArticleCard">
            <Link to={`/${frontmatter.slug}`}>
                <div className="ImgContainer">
                    <Image src={src} alt={frontmatter.title} options={options} />
                </div>
                <div className="TextContainer">
                    <div className="Title">{frontmatter.title}</div>
                    <div className="Date">{frontmatter.date}</div>
                    <p className="Excerpt">{data.excerpt}</p>
                    <div className="AuthorBubbleContainer">
                        <Author id={frontmatter.author} />
                    </div>
                </div>
            </Link>
        </article>
    );
};

export default Card;
